import React, { useEffect } from 'react';
import { Button, Input } from 'reactstrap';

export const Rep = ({ item, onItemChange }) => {
  useEffect(() => {}, []);

  return (
    <tr>
      <th scope="row">{item[0]}</th>
      <td>
        {item.editable ? (
          <Input
            type={item.num.type}
            value={item.num.value}
            onChange={event =>
              onItemChange({ ...item, num: { value: event.target.value, type: item.num.type } })
            }

            
          />
        ) : (
          item.num.value
        )}
      </td>
      <td>
        {item.editable ? (
          <Input
            type={item.weigth.type}
            value={item.weigth.value}
            onChange={event =>
              onItemChange({
                ...item,
                weigth: { value: event.target.value, type: item.weigth.type },
              })
            }
          />
        ) : (
          item.weigth.value
        )}
      </td>
      <td>
        {/* <Button
          color={!item.editable ? 'primary' : 'danger'}
          onClick={
            !item.editable
              ? onItemChange({ ...item, editable: true })
              : onItemChange({ ...item, editable: false })
          }
        >
          {!item.editable ? 'edit' : 'done'}
        </Button> */}
      </td>
    </tr>
  );
};
